export const entitiesByName = {};

Object.keys(entitiesByCodePoint).map((codePoint) => {
	const name = entitiesByCodePoint[codePoint];
	entitiesByName[name] = Number(codePoint);
});

export const getEntityCodePoint = (name) => {
	return name in entitiesByName ?
		entitiesByName[name] :
		null;
};

export const getEntityName = (codePoint) => {
	return codePoint in entitiesByCodePoint ?
		entitiesByCodePoint[codePoint] :
		null;
};



